import React from 'react';
import ReactDOM from 'react-dom/client';
import {
	createBrowserRouter,
	RouterProvider,
} from 'react-router-dom';
import './index.css';
import Root from './routes/root';
import ChatContainer from './components/chatContainer/chatContainer';
import ErrorPage from './error-page';
import ChatContainerIndex from './components/chatContainer/chatContainerIndex';
import { SocketProvider } from './contexts/socket/socketContext';
import { AuthProvider } from './contexts/auth/authContext'; 
import Login from './routes/login';
import { MessengerProvider } from './contexts/messenger/messengerContext';
import 'bootstrap/dist/css/bootstrap.min.css';
import Register from './routes/register';

const router = createBrowserRouter([
	{
		// path: "/",
		path: process.env.REACT_APP_PATH_ROUTE_ROOT,
		element: <Root />,
		errorElement: <ErrorPage />,
		children: [
			{index: true, element: <ChatContainerIndex />},
			{
				errorElement: <ErrorPage />,
				children: [
					{
						// path: "/:chatId",
						path: process.env.REACT_APP_PATH_ROUTE_CHAT,
						element: <ChatContainer />,
					},
				],
			}
		],
	},
	{
		// path: "/login",
		path: process.env.REACT_APP_PATH_ROUTE_LOGIN,
		element: <Login />,
		errorElement: <ErrorPage />,
	},
	{
		// path: "/register",
		path: process.env.REACT_APP_PATH_ROUTE_REGISTER,
		element: <Register />,
		errorElement: <ErrorPage />,
	},
]);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
	<AuthProvider>
		<SocketProvider>
			<MessengerProvider>
				<RouterProvider router={router} />
			</MessengerProvider>
		</SocketProvider>        
	</AuthProvider>
);
